/* Roboto font */

* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    /* border: 1px solid orange; */
}

#menuWrapper {
    display: flex;
    flex-direction: column;
    width: 100vw;
    height: 100vh;
    background-image: url(${___CSS_LOADER_URL_REPLACEMENT_0___});
    background-size: cover;
    background-repeat: no-repeat;
    background-position: center -800px;
    align-items: center;
    margin: 0px;
    padding: 0px;
}

#menuTopBarContainer {
    background-color: black;
    display: grid;
    grid-template-columns: 2fr 8fr 1fr;
    width: 100vw;
}

#menuTopBarContainer > * {
    margin: 10px;
}

#menuLogo {
    font-family: 'Dancing Script';
    color: white;
    margin-left: 20px;
    cursor: pointer;
}

#menuAboutButton {
    grid-column: 3;
    background-color: rgb(75, 0, 0);
    color: white;
    font-family: "Roboto" sans-serif;
    font-size: medium;
    border: 0px;
    border-radius: 10px;
}

#menuAboutButton:hover {
    background-color: red;
}

#locationsContainer {
    width: 100%;
    border: 1px solid orange;
}

.sectionContainer {
    border: 5px solid rgb(88, 0, 0);
    border-radius: 55px;
    background-color: rgb(15, 15, 15);
}

.sectionContainer > * {
    margin: 30px;
}

#menuItemsContainer {
    width: 50vw;
    overflow: auto;
}

#menuItemContainer {
    display: grid;
    grid-template-columns: 1fr 3fr;
    position: relative;
    gap: 20px;
}

#menuItemImage {
    grid-column: 1;
    max-width: 90%;
    border-radius: 100%;
}

#menuItemTextContainer {
    display: grid;
}

#menuTitle, 
#menuDescription,
#menuPrice {
    color: white;
    padding: 10px;
    font-family: 'Roboto' sans-serif;
}

#menuPrice {
    font-weight: bold;
}

#menuDescription {
    letter-spacing: 2px;
}

#menuTitle {
    border-bottom: 1px solid red;
}

.menuItemsHeader {
    color: white;
    font-family: "Roboto" sans-serif;
    font-size: 30px;
}

