/* Roboto font (for hook) */

body {
    margin: 0;
    padding: 0;
}

#landingWrapper {
    background-image: url(${___CSS_LOADER_URL_REPLACEMENT_0___});
    background-size: cover;
    background-repeat: no-repeat;
    background-position: center -800px;
    display: grid;
    grid-template-rows: 1fr 1fr 1fr;
    width: 100vw;
    height: 100vh;
}

#logoContainer {
    display: grid;
    grid-row: 2;
    grid-template-rows: 3fr 1fr;
    justify-content: center;
    background-color: black;
    align-items: center;
    border-bottom: 4px solid grey;
}

#restaurantLogo {
    justify-content: center;
    align-items: center;
    color: rgb(255, 255, 255);
    font-family: 'Dancing Script';
    font-size: 100px;
}

#buttonContainer {
    display: flex;
    justify-content: center;
}

#menuButton, 
#aboutButton {
    width: 30%;
    height: 100%;
    border: 2px solid grey;
    font-family: "Roboto";
    font-size: large;
    margin: 0 5px;
    margin-bottom: 10px;
    color: white;
    background-color: rgb(48, 0, 0);
    transition: all 0.3s;
}

#menuButton:hover, 
#aboutButton:hover {
    background-color: red;
    cursor: pointer;
    width: 31%;
}

#hookContainer {
    display: grid;
    grid-template-rows: repeat(3, 1fr);
    color: white;
    grid-row: 3;
    text-align: center;
    background: linear-gradient(to bottom);
}

#hook {
    font-family: 'Roboto';
    font-size: 25px;
    text-shadow: -1px 0 black, 0 1px black, 1px 0 black, 0 -1px black;
}

#openHours {
    display: flex;
    flex-direction: column;
}

#openHours > span {
    font-family: "Roboto";
    color: rgb(231, 231, 231);
}

#bgImageAttribution {
    font-family: 'Roboto';
    font-size: small;
    margin-bottom: 10px;
    align-self: flex-end;
}










